export default {
    props: {
        paper: {
            type: Object,
            required: true,
        },
    },
    computed: {
        journalName() {
            if(!this.paper.journal || !this.paper.journal.name)
                return this.paper.journal_old
            return this.paper.journal.name
        },
        isMega() {
            if(this.paper.support != 'Megagrant')
                return false
            if(this.paper.affiliation != 'Skoltech')
                return false
            if(!this.paper.journal || !this.paper.journal.if)
                return false
            return true
        },
        checkDate() {
            if(!this.paper.journal || !this.paper.journal.check_date)
                return ''
            let d = this.paper.journal.check_date
            let r = this.$moment(d.split('T')[0], 'YYYY.MM.DD')
            this.$moment.locale('en');
            return r.format('MMMM D, YYYY')
        },
        support() {
            if(this.paper.support != 'Megagrant')
                return ``
            return this.isENG
                ? `Is supported by "Megagrant" funding`
                : `Работа выполнена при поддержке "МегаГрантом"`
        },
        authors() {
            if(!this.paper.author)
                return ''
            let authors = this.paper.author.split(' and ').map((a, i, A) => {
                a = a.split(',')
                let name = a[1] ? a[1].trim() : ''
                name = name.length == 1 ? name + '.' : name
                let surname = a[0] && a[0] != 'others' ? a[0].trim() : ''
                let isETC = a[0] == 'others'
                let isInTeam = true
                if(!surname || !this.teamTbl.isLoaded) {
                    isInTeam = false
                }
                else {
                    if(!this.man) {
                        try { this.teamTbl.row(surname.toLowerCase()) }
                        catch(err) { isInTeam = false }
                    }
                    else {
                        isInTeam = this.man.surname == surname
                    }
                }
                let isLast = i == A.length-1
                return {name, surname, isETC, isInTeam, isLast}
            })
            return authors
        },
        bib() {
            let bib = []
            if(this.paper.title)
                bib.push(`title={${this.paper.title}}`)
            if(this.paper.author)
                bib.push(`author={${this.paper.author}}`)
            if(this.paper.journal && this.paper.journal.name)
                bib.push(`journal={${this.paper.journal.name}}`)
            if(this.paper.volume)
                bib.push(`volume={${this.paper.volume}}`)
            if(this.paper.number)
                bib.push(`number={${this.paper.number}}`)
            if(this.paper.pages)
                bib.push(`pages={${this.paper.pages}}`)
            if(this.paper.year)
                bib.push(`year={${this.paper.year}}`)
            if(this.paper.journal && this.paper.journal.publisher)
                bib.push(`publisher={${this.paper.journal.publisher}}`)
            if(this.paper.doi)
                bib.push(`doi={${this.paper.doi}}`)
            if(this.paper.url)
                bib.push(`url={${this.paper.url}}`)
            return `@article{${this.paper.tag},\n  ` + bib.join(',\n  ') + `\n}`
        },
    },
}



