<script>

/*
v-snackbar(
    :timeout="2000"
    :bottom="true"
    :center="true"
    :multi-line="false"
    :vertical="false"
    v-model="isCopied")
    span(v-if="errCopy") Can't copy bibtex record to clipbord
    span(v-if="isENG && !errCopy") Bibtex record is saved to clipbord
    span(v-if="isRUS && !errCopy") Bibtex запись сохранена в буфер
    v-btn(
        :flat="true"
        color="pink"
        @click.native="isCopied = false") Close
*/

import PaperMixin from './_PaperMixin'

export default {
    name: 'dt-paper-card',
    mixins: [PaperMixin],
    props: {
        man: {
            type: Object,
            default: _ => undefined,
        },
    },
    data() {
        return {
            isCopied: false,
            errCopy: '',
        }
    },
    methods: {
        onCopy(e) {
            this.isCopied = true
        },
        onCopyError(e) {
            this.isCopied = true
        },
    },
}
</script>

<template lang="pug">

dkbr-frame.dt-paper-card(
    th="th7"
    :isThInv="true"
    :elevation="20")
    p.dt-paper-card__header(
        v-if="!man")
        dkbr-icon.dt-paper-card__icon(
            name="local_library")
        span {{ paper.year }} {{ support ? ' | ' + support : '' }}
    p.dt-paper-card__title
        | {{ paper.title }} // {{ journalName }} [ {{ paper.date }}]
    div.dt-paper-card__box
        div.dt-paper-card__content
            p.dt-paper-card__author
                span(
                    v-for="(author, i) of authors"
                    :key="i"
                    :class=`{'dt-paper-card__author_person': author.isInTeam}`) {{ author.name }} {{ author.isETC ? 'and others' : '' }}{{ author.surname }}{{ author.isLast ? '.' : ', '}}
        div.dt-paper-card__actions
            dkbr-button.dt-paper-card__action(
                th="th3"
                :href="paper.url"
                target="_blank"
                :isRaised="true"
                :isDense="true"
                :desc="isENG ? 'Go to journal' : 'На страницу журнала'"
                tltpDir="top"
                icon="public")
            dkbr-button.dt-paper-card__action(
                th="th3"
                :isRaised="true"
                :isDense="true"
                :desc="isENG ? 'Copy Bibtex record' : 'Копировать Bibtex'"
                tltpDir="top"
                icon="code"
                v-clipboard:copy="this.bib"
                v-clipboard:success="onCopy"
                v-clipboard:error="onCopy")
</template>

<style lang="sass">

@import '~@dt/sass/root.sass'

.dt-paper-card
    +margin(10px)
    +padding(5px 20px)
    &__box
        +flex(row, nowrap, space-between, center)
    &__content
        flex: 1 0 200px
    &__header
        +color-font(dark3)
        +font(base-bold-small)
    &__icon
        +margin-r(10px)
    &__title
        +margin(0)
        +padding(2px 0 5px)
        +color-font(dark3)
        +font(base-bold-big)
    &__author
        +margin(3px 10px 20px)
        +padding(0 20px)
        &_person
            +color-font(light3)
            +font(base-bold)
    &__actions
        flex: 0 0 50px
        +flex(row, nowrap, flex-end)
        +padding-r(10px)
    &__action
        position: relative
        display: block
        +margin(0 10px)
        +padding(0)

</style>



