<script>

import DemoBulder from './DemoBuilder'
import Icon from '@@/icon/vue/Icon'
import Button from '@@/button/vue/Button'
import ButtonDial from '@@/button/vue/ButtonDial'
import Progress from '@@/progress/vue/Progress'
import Tooltip from '@@/tooltip/vue/Tooltip'
import Input from '@@/input/vue/Input'
import Figure from '@@/figure/vue/Figure'
import Frame from '@@/frame/vue/Frame'

import Float from '@@/float/vue/Float'
import List from '@@/list/vue/List'

export default {
    name: 'dkbr-demo',
    path: '/demo',
    title: 'Demo',
    head: {
        title() {
            return {
                inner: this.isENG ? 'Demo' : 'Demo',
                separator: '|',
                complement: 'DKBR',
            }
        },
        link: [
            {
                rel: 'icon',
                href: 'static/pictures/dt/logo/favicon.png',
                sizes: '16x16',
                type: 'image/png',
            },
        ],
    },
    components: {
        'dkbr-demo-builder': DemoBulder,
    },
    data() {
        return {
            Elem: Figure,
            Elems: {
                Icon, Button, ButtonDial, Progress, Tooltip, Input, Figure,
                Frame, Float, List,
            },
        }
    },
}
</script>

<template lang="pug">

div.demo
    dkbr-button-dial(
        th="th2"
        posx="right"
        posy="bottom"
        dir="top"
        effect="fling"
        icon="play_for_work"
        iconFinal="add"
        md-direction="top"
        :isHover="false")
            md-button.md-raised.md-primary(
                v-for="[name, El] of Object.entries(Elems)"
                :key="name"
                style="width: 100px;"
                @click="Elem = El") {{ name }}
    dkbr-demo-builder.demo__content(
        v-if="El == Elem"
        v-for="[name, El] of Object.entries(Elems)"
        :key="name"
        :Elem="El")
</template>

<style lang="sass">

.demo
    &__content
        width: 100vw
        height: 100vh
        margin: 0
</style>



