import Vue from 'vue'
import VueRouter from 'vue-router'
import mixin_ from './mixin'
import _ from './components'
import App from './Dikobraz'

Vue.config.productionTip = false
Vue.use(VueRouter)
mixin_('deeptensor', true)

const app = new Vue({
    el: '#app',
    template: '<App/>',
    components: {App},
    router: new VueRouter({mode: 'history', routes: App.routes}),
    render: function(h) { return h(App) }
})



