<script>

import Papers from '@dt/components/db/papers/Papers'

export default {
    name: 'dt-page-publications',
    path: 'publications',
    title: 'Publications',
    head: {
        title() {
            return {
                inner: this.isENG ? 'Publications' : 'Публикации',
                separator: '|',
                complement: 'DT',
            }
        },
        meta() {
            return [
                {
                    name: 'keywords',
                    content: this.isENG
                        ? 'Tensor networks, deep learning, neural networks, megagrant, skoltech, Andrzej Cichocki, Ivan Oseledets,  Victor Lempitsky,  Dmitry Vetrov, publications, WoS, Scopus'
                        : 'Тензорные сети, глубинное обучение, глубокое обучение, нейронные сети, мегагрант, Сколтех, Анджей Чихоцкий, Иван Оселедец, Виктор Лемпицкий, Дмитрий Ветров, публикации, WoS, Scopus',
                },
                {
                    name: 'description',
                    content: this.isENG
                        ? 'Skoltech | Laboratory of Prof. Andrzej Cichocki | Tensor networks and deep learning for applications in data mining | Publications'
                        : 'Сколтех | Лаборатория Тензорные сети и глубинное обучение для интеллектуального анализа данных под руководством доктора Анджея Чихоцкого | Публикации',
                },
            ]
        },
        link: [
            {
                rel: 'icon',
                href: 'static/pictures/dt/logo/favicon.png',
                sizes: '16x16',
                type: 'image/png',
            },
        ],
    },
    components: {
        'dt-papers': Papers,
    },
}
</script>

<template lang="pug">

dt-papers.dt-page-publications(
    :man="undefined"
    :years="[2018, 2017]")
</template>



