<script>

//import DbMixin from './../_DbMixin'
import Person from './Person'

export default {
    name: 'dt-team',
    //mixins: [DbMixin],
    components: {
        'dt-person': Person,
    },
    props: {
        mod: {
            type: String,
            enum: ['list', 'grid'],
            default: _ => 'grid'
        },
    },
    data() {
        return {
            CLASS: 'Team',
            isPersonActive: false,
            person_: {},
        }
    },
    computed: {
        head() {
            return this.teamTbl.rows.find(m => m.surname == 'Cichocki')
        },
        rolesSub() {
            return [
                {
                    name: 'deputy',
                    title: this.isENG
                        ? 'Leaders of the research directions'
                        : 'Руководители научных направлений',
                    imgW: 250
                },
                {
                    name: 'researcher',
                    title: this.isENG
                        ? 'Researchers'
                        : 'Научные сотрудники',
                    imgW: 225
                },
                {
                    name: 'phdstudent',
                    title: this.isENG
                        ? 'Ph.D. students'
                        : 'Аспиранты',
                    imgW: 200
                },
                {
                    name: 'student',
                    title: this.isENG
                        ? 'Students'
                        : 'Студенты',
                    imgW: 175
                }
            ]
        },
        isFirst() {
            const i = this.teamTbl.rows.findIndex(p => p == this.person_)
            return i == 0
        },
        isLast() {
            const i = this.teamTbl.rows.findIndex(p => p == this.person_)
            return i == (this.teamTbl.rows.length - 1)
        },
    },
    methods: {
        filter_(person) {
            return person && person.active
        },
        sort_() {
            return
            this.teamTbl.rows.sort((p1, p2) => {
                if(p1.order < p2.order)
                    return -1
                return 1
            })
            //this[tblName]
        },
        personImg(person, isThumb=false) {
            let img = `static/pictures/dt/team/team`
            img+= `_${person.surname.toLowerCase()}_${person.name.toLowerCase()}`
            img+= isThumb ? `_thumb` : ''
            img+= `.jpg`
            return img
        },
        open(person_) {
            console.log('open person ', person_)
            this.person_ = person_
            this.isPersonActive = !!this.person_
        },
        openNext() {
            if(!this.person_)
                return
            let i = this.teamTbl.rows.findIndex(m => m == this.person_)
            return this.open(this.teamTbl.rows[i+1])
        },
        openPrev() {
            if(!this.person_)
                return
            let i = this.teamTbl.rows.findIndex(m => m == this.person_)
            return this.open(this.teamTbl.rows[i-1])
        },
        close() {
            this.person_ = undefined
            this.isPersonActive = false
        },
    }
}
</script>

<template lang="pug">

div.dt-team(:class=`['dt-team_'+mod]`)
    dkbr-progress(
        v-if="!teamTbl.isLoaded || !teamTbl.isParsed"
        :isInf="true")
    div(
        v-if="isPersonActive && person_")
        div.dt-team__slider.slider
            div.slider__button.slider__button_arrow-left(
                v-if="!isFirst" @click="openPrev()")
                dkbr-icon(name="keyboard_arrow_left" :size="2")
                dkbr-tooltip(dir="right"
                    :text="isENG ? 'To the previous' : 'К предыдущему'")
            div.slider__button.slider__button_close(
                v-if=" isFirst" @click="close()")
                dkbr-icon(name="close" :size="2")
                dkbr-tooltip(dir="bottom"
                    :text="isENG ? 'Close' : 'Закрыть'")
            div.slider__dots
                div.slider__button.slider__button_dot(
                    v-if="filter_(person)"
                    v-for="person of teamTbl.rows"
                    :key="person.id"
                    :class=`{'slider__button_dot_active': person == person_}`
                    @click="open(person)")
                    dkbr-tooltip(dir="bottom"
                        :text="isENG ? person.name + ' ' + person.surname : person.name_rus + ' ' + person.surname_rus")
            div.slider__button.slider__button_arrow-right(
                v-if="!isLast" @click="openNext()")
                dkbr-icon(name="keyboard_arrow_right" :size="2")
                dkbr-tooltip(dir="right"
                    :text="isENG ? 'To the next' : 'К следующему'")
            div.slider__button.slider__button_close(
                v-if=" isLast" @click="close()")
                dkbr-icon(name="close" :size="2")
                dkbr-tooltip(dir="bottom"
                    :text="isENG ? 'Close' : 'Закрыть'")
        dt-person(:person="person_")
    div.duplex.duplex_reverse(
        v-if="!isPersonActive")
        div.duplex__item.duplex__item_small(v-if='head')
            h2.dt-team__title.dt-title.dt-title_3d-wide
                span(v-if="isENG") Head of the Laboratory
                span(v-if="isRUS") Руководитель Лаборатории
            div.dt-figure.button-shadow3d(
                tag="a"
                :style="{cursor: 'pointer'}"
                @click="_ => open(head)")
                dkbr-figure(
                    :src="personImg(head)")
                    p.dt-team__title-img.link {{ head.name }} {{ head.surname }}
        aside.duplex__item.duplex__item_top
            div.dt-team__aside.text
                p.dt-text
                    span(v-if="isENG") The Laboratory brings together several professors and young researchers in the fields of machine learning, computer vision, artificial intelligence, robotics, large scale data analysis, mathematics as well as computational neuroscience and bioinformatics.
                    span(v-if="isRUS") Наш коллектив объединяет профессоров и молодых исследователей, являющихся специалистами мирового уровня в области машинного обучения, компьютерного зрения, искусственного интеллекта, робототехники, анализа  больших объемов данных, а также вычислительной математики, нейробиологии и биоинформатики.
                p.dt-text
                    span(v-if="isENG") We opened several research positions for various levels including assistant professors, postdoc and  PhD students. The Laboratory  offers a unique research program and also PhD fellowship program.
                    span(v-if="isRUS") В Лаборатории есть открытые позиции для научных сотрудников, аспирантов и студентов.
                router-link.dt-em.dt-em_spec.dt-em_right.link(
                    style="text-align: right;"
                    tag="a",
                    :to=`{
                        name: 'dt-page-events',
                        params: {name: 'num6'}
                    }`) {{ isENG ? 'More info' : 'Подробнее' }}
    div(
        v-if="!isPersonActive"
        v-for="role of rolesSub"
        :key="role.name")
        h2.dt-team__title.dt-title.dt-title_3d-wide
            | {{ role.title }}
        section.dt-team_gallery.gallery
            div.gallery__item.button-shadow3d(
                tag="a",
                v-for="person of teamTbl.rows"
                :key="person.surname"
                v-if="person.active && person.role == role.name"
                :style="{order: person.order, cursor: 'pointer'}"
                @click="_ => open(person)")
                dkbr-figure(
                    :src="personImg(person, true)"
                    :w="role.imgW")
                    p.dt-team__title-img.link
                        | {{ person.name }}
                        br(v-if="person.role == 'head' || person.role == 'deputy'")
                        |
                        | {{ person.surname }}
</template>

<style lang="sass">

@import '~@/sass/root.sass'

.dt-team
    &__progress
        position: absolute
        left: 0
        top: 0
        padding: 0
        margin: 0
    &__title
        +margin(50px 0 40px 30px)
        &-img
            text-align: center
    &__aside
        +margin(20px)
.duplex
    +flex(row, wrap, space-around, center)
    &_reverse
        flex-wrap: wrap-reverse
    &__item
        flex: 1 0 300px
        +flex-basis($micro: 250px)
        +margin(20px)
        &_main
            flex: 2 0 60%
        &_small
            flex: 0 1 300px
        &_top
            align-self: flex-start
.gallery
    +flex(row, wrap, space-around, flex-start)
    padding: 5px
    &__item
        +margin(30px)
    &_small
        flex: 0 1 200px

.slider
    +flex(row, nowrap, space-between)
    +padding(20px)
    &__dots
        +flex(row, wrap, space-around, center)
    &__button
        cursor: pointer
        &_arrow-left
        &_arrow-right
        &_close
        &_dot
            width: 10px
            height: 10px
            margin: 10px
            border: 7px solid rgb(107, 126, 81)
            border-radius: 50%
            &:hover
                border: 7px solid rgb(0, 58, 86)
            &_active
                border: 10px solid rgb(0, 58, 86)
                cursor: default
                &:hover
                    border: 10px solid rgb(0, 58, 86)
</style>



