<script>

import Header from './components/Header'
import Nav from './components/Nav'
import Footer from './components/Footer'

import Index from './pages/Index'
import Team from './pages/Team'
import Events from './pages/Events'
import Publications from './pages/Publications'
import Contacts from './pages/Contacts'
import Tmp from './pages/Tmp'

import('@/node_modules/vuetify/dist/vuetify.min.css')

export default {
    name: 'dt',
    path: '',
    title: 'Deeptensor',
    routes: [Index, Team, Events, Publications, Contacts, Tmp].map(comp => ({
        name: comp.name,
        path: comp.name != 'dt-page-index' ? comp.name.split('dt-page-')[1] : '',
        component: comp,
        children: comp.routes || []
    })),
    components: {
        'dt-header': Header,
        'dt-nav': Nav,
        'dt-footer': Footer,
    },
    data() {
        return {
            CLASS: 'Deeptensor',
        }
    },
    computed: {
        pages() {
            return [Index, Team, Events, Publications, Contacts].map(comp => ({
                name: comp.name,
                title: comp.head.title.bind(this)().inner,
            }))
        },
    },
    created() {
        try{
            this.ST._load()
            this.CS.res(
                `Settings are loaded`, this.CLASS, 'created')
        }
        catch(err) {
            this.CS.err(
                `Can't load settings`, this.CLASS, 'created', err)
        }
    },
    beforeDestroy() {
        try{
            this.ST._save()
            this.CS.res(
                `Settings are saved`, this.CLASS, 'beforeDestroy')
        }
        catch(err) {
            this.CS.err(
                `Can't save settings`, this.CLASS, 'beforeDestroy', err)
        }
    },
    methods: {
        toggleLang(mod=undefined) {
            if(mod=='over') {
                if(this.langSt_.isOver)
                    return
                else
                    this.langSt_.isOver = true
            }
            if(mod=='leave') {
                this.langSt_.isOver = false
                if(this.langSt_.isClicked) {
                    this.langSt_.isClicked = false
                    return
                }
            }
            if(mod=='click') {
                this.langSt_.isClicked = true
                if(this.langSt_.isOver)
                    return
            }
            this.ST._set('lang', this.isENG ? 'rus' : 'eng')

        },
    }
}
</script>

<template lang="pug">

v-app.dt-page
    dt-header.dt-page__header
    dt-nav.dt-page__nav(
        :routes_="pages")
    div.dt-page__content
        router-view
    dt-footer.dt-page__footer
</template>

<style lang="sass">

@import '~@dt/sass/root.sass'

html
    overflow-x: visible
    overflow-y: scroll
body,
body.md-theme-deeptensor
    position: relative
    width: auto
    min-height: 100vh
    margin: 0
    overflow: scroll
    +padding(0 10px, $nano: 0 5px)
    +color-back(light1)
div#__nuxt
    overflow: scroll
    min-width: $width-min
    max-width: $width-max
    width: 100%
    margin: 0 auto
div#app
    overflow-x: visible
.dt-page
    +flex(column)
    +color-back(light1)
    &__content
        position: relative
        flex: 1 0 0
        +padding(15px 30px, $milli: 10px 20px, $nano: 7px 12px)
        +color(light, dark)
        +font(base)
.button-shadow3d
    +flex(row, nowrap, center, center)
    +border(light)
    +color-font(dark3)
    +font(base-huge)
    &:hover
        +border(dark)
        +shadow(box3d-dark)
        +color-font(light3)
.dt-title
    +display(inline-block)
    +padding(10px 0 4px)
    +color-font(dark3)
    +font(base-bold-big)
    &_sub
        +padding(6px 0 2px)
        +color-font(dark2)
        +font(base-bold)
    &_pd
        +padding(10px 0 8px)
    &_3d
        +padding(20px 10px 20px 20px, $micro: 15px 30px 15px 80px, $nano: 10px 20px 10px 50px)
        +shadow(box3d-dark)
        +color(light1, dark3)
        +font(base-huge)
    &_3d-wide
        +padding(20px 50px 20px 100px, $micro: 15px 30px 15px 80px, $nano: 10px 20px 10px 50px)
        +shadow(box3d-dark)
        +color(light1, dark3)
        +font(base-huge)
.dt-link
    +color-font(dark)
    +font(base)
    text-decoration: underline
    cursor: pointer
    &:hover
        +color-font(light3)
        text-decoration: none
        cursor: pointer
.dt-em
    +color-font(light3)
    +font(base)
    text-align: left
    &_spec
        +color-font(dark3)
        +font(base-italic)
    &_right
        text-align: right
.dt-text
    +margin(5px 0 1px)
    +color-font(dark)
    +font(base)
    text-align: justify
.dt-string
    +margin(3px 0 1px)
    +color-font(dark)
    +font(base)
    text-align: left
</style>



