import Vue from 'vue'

import VueMaterial from 'vue-material'
Vue.use(VueMaterial)

import Vuetify from 'vuetify'
Vue.use(Vuetify)

import VueMoment from 'vue-moment'
Vue.use(VueMoment)

import VueClipboard from 'vue-clipboard2'
Vue.use(VueClipboard)

import 'vue-googlemaps/dist/vue-googlemaps.css'
import VueGoogleMaps from 'vue-googlemaps'
Vue.use(VueGoogleMaps, {
  load: {
    apiKey: 'AIzaSyB0HvlqbtfXpnS1rO4xMxXzPVrGQlWoVYA',
    libraries: ['places'],
  },
})

import VueHead from 'vue-head'
Vue.use(VueHead)

import dkbrIcon from '@@/icon/vue/Icon.vue'
Vue.component('dkbr-icon', dkbrIcon)

import dkbrButton from '@@/button/vue/Button.vue'
Vue.component('dkbr-button', dkbrButton)

import dkbrButtonDial from '@@/button/vue/ButtonDial'
Vue.component('dkbr-button-dial', dkbrButtonDial)

import dkbrProgress from '@@/progress/vue/Progress'
Vue.component('dkbr-progress', dkbrProgress)

import dkbrTooltip from '@@/tooltip/vue/Tooltip'
Vue.component('dkbr-tooltip', dkbrTooltip)

import dkbrInput from '@@/input/vue/Input'
Vue.component('dkbr-input', dkbrInput)

import dkbrFigure from '@@/figure/vue/Figure'
Vue.component('dkbr-figure', dkbrFigure)

import dkbrFrame from '@@/frame/vue/Frame'
Vue.component('dkbr-frame', dkbrFrame)



Vue.prototype.$bus = new Vue({
    methods: {
        on(name, func) {
            return this.$on(name, func)
        },
        once(name) {
            return this.$once(name)
        },
        off(name, func) {
            return this.$off(name, func)
        },
        emit(name) {
            return this.$emit(name)
        }
    }
})



