<script>

export default {
    name: 'dt-event',
    props: {
        event: {
            type: Object,
            default: _ => {},
        },
        isShowContent: {
            type: Boolean,
            default: _ => false,
        },
    },
    computed: {
        date() {
            if(!this.event || !this.event.date)
                return
            let r = this.$moment(this.event.date.split('T')[0], 'YYYY.MM.DD')
            if(this.isENG) {
                this.$moment.locale('en');
                return r.format('MMMM D, YYYY')
            }
            if(this.isRUS) {
                this.$moment.locale('ru')
                return r.format('D MMMM, YYYY')
            }
        },
        img() {
            if(this.get_('image1'))
                return 'static/pictures/dt/' + this.get_('image1')
        },
        imgs() {
            let imgs = this.get_('images')
            imgs = imgs ? imgs.split('; ') : []
            if(imgs.length == 0 && this.event.paper)
                imgs.push('events/paper.png')
            imgs = imgs.map(img => 'static/pictures/dt/' + img)
            return imgs
        },
        title() {
            if(this.get_('title'))
                return this.get_('title')
            if(this.event.paper)
                return `New paper "${this.event.paper.title}"`
            return ''
        },
        description() {
            if(this.get_('description'))
                return this.get_('description')
            if(this.event.paper)
                return `Abstract. ` + this.event.paper.abstract
            return ''
        },
        descriptionSub() {
            if(this.get_('description_sub'))
                return this.get_('description_sub')
            if(this.get_('description') && this.event.paper)
                return `Abstract. ` + this.event.paper.abstract
            return ''
        },
    },
    methods: {
        get_(opt) {
            if(this.isRUS && this.event[opt + '_rus'] !== undefined)
                return this.event[opt + '_rus']
            return this.event[opt]
        },
    },
}
</script>

<template lang="pug">

dkbr-frame.dt-event(
    th="th7"
    :isThInv="true"
    :elevation="10")
    div.dt-event__header
        dkbr-icon(
            :isThInv="true"
            name="date_range"
            :text="date")
    div.dt-event__title {{ title }}
    div.dt-event__content(
        v-if="isShowContent")
        dkbr-figure.dt-event__content-image(
            v-if="isShowContent && imgs.length",
            :srcs="imgs"
            :isAuto="true"
            :autoDt="10000"
            :animDt="1000"
            :w="500"
            :ws="350"
            :hs="400"
            :sp="200"
            :scaling="20"
            :perspective="50"
            :count="2")
        div.dt-event__content-text
            div.dt-event__content-text-item(v-html="description")
    div.dt-event__content-text-item(
        v-if="isShowContent"
        v-html="descriptionSub")
    div.dt-event__footer
        dkbr-button(
            v-if="!isShowContent"
            :isThInv="true"
            :isRaised="true"
            :isDense="true"
            :text="isENG ? 'More info' : 'Подробнее'"
            @click.native="$emit('click')")
        div.dt-event__links(
            v-if="isShowContent")
            div.dt-event__links-item(
                v-if="link"
                v-for="(link, i) in [get_('link1'), get_('link2')]")
                dkbr-icon(name="link" :size="1")
                dkbr-button(
                    :isThInv="true"
                    :isRaised="false"
                    :isDense="true"
                    :text="get_(`link${i+1}_text`)"
                    :href="link"
                    target="_blank")
</template>

<style lang="sass">

@import '~@dt/sass/root.sass'

.dt-event
    +flex(column)
    +padding(10px 10px 2px)
    +border(light-top)
    &__header
        +padding(2px)
        +font(extra)
        +color-font(light3)
    &__title
        flex: 1 0 100px
        +padding(10px 0 4px)
        +color-font(dark3)
        +font(base-bold)
    &__content
        +flex(row, wrap-reverse, space-around, flex-end)
        +padding(10px)
        &-text
            flex: 1 0 300px
            &-item
                +padding(20px)
                +font(base)
                text-align: justify
        &-image
            flex: 1 0 300px
    &__footer
        +flex(row, wrap, flex-end, center)
    &__links
        flex: 1 0 300px
        +flex(column, wrap, flex-start)
        &-item
            +flex(row, nowrap, flex-start, center)
</style>



