<template lang="pug">

    router-view
</template>

<script>

import Deeptensor from './deeptensor/Deeptensor'

export default {
    name: 'v-site',
    path: '',
    title: 'Site',
    routes: [Deeptensor].map(comp => ({
        name: comp.name,
        path: comp.path,
        title: comp.title,
        component: comp,
        children: comp.routes || []
    })),
}
</script>



