<script>

import Papers from './../papers/Papers'

export default {
    name: 'dt-person',
    components: {
        'dt-papers': Papers,
    },
    props: {
        person: {
            type: Object,
            default: {},
        },
    },
    data() {
        return {
            isShowMail: false,
        }
    },
    computed: {
        img() {
            let img = `static/pictures/dt/team/team`
            img+= `_${this.person.surname.toLowerCase()}`
            img+= `_${this.person.name.toLowerCase()}`
            img+= `.jpg`
            return img
        },
        imgThumb() {
            let img = this.img()
            img.replace(`.jpg`, `_thumb.jpg`)
            return img
        },
        cv() {
            let s = this.person.surname
            let n = this.person.name
            return `static/pdf/dt/pdf/CV_${s}_${n}.pdf`
        },
    },
    methods: {
        get_(opt) {
            if(this.isRUS && this.person[opt + '_rus'] !== undefined)
                return this.person[opt + '_rus']
            return this.person[opt]
        },
        toggleShowMail() {
            this.isShowMail = !this.isShowMail
        },
        papersCount(y) {
            return this.papersTbl.rows.filter(p => ~p.author.indexOf(this.person.surname) && p.year == y).length
        },
    },
}
</script>

<template lang="pug">

div.dt-person
    div.dt-person__main
        dkbr-figure.dt-person__main-photo(
            :src="img")
        md-list.dt-person__main-info(
            md-theme="th3i"
            :class=`{'md-primary': true,}`)
            md-list-item
                span.dt-person__title {{ get_('name') + ` ` + get_('surname') }}
            md-divider
            md-list-item(
                v-if="get_('degree')")
                span.dt-person__field {{ get_('degree') }}
            md-list-item(
                v-if="get_('position')")
                span.dt-person__field {{ get_('position') }}
            md-list-item(
                v-if="get_('institution')")
                span.dt-person__field {{ get_('institution') }}
            md-divider
            md-list-item(
                :href="cv"
                target="_blank")
                span.dt-person__field
                    | {{ isENG ? 'CV' : 'Резюме' }}
                dkbr-icon(
                    name="file_download")
            md-list-item(
                v-if="get_('home_page')"
                :href="get_('home_page')"
                target="_blank")
                span.dt-person__field
                    | {{ isENG ? 'Home page' : 'Домашняя страница' }}
                dkbr-icon(
                    name="public")
            md-list-item(
                v-if="get_('mail') && !isShowMail"
                @click="toggleShowMail")
                span.dt-person__field(
                    v-if="!isShowMail")
                    | {{ isENG ? 'Email' : 'Электронная почта' }}
                span.dt-person__field(
                    v-if=" isShowMail")
                    | {{ get_('mail') }}
                dkbr-icon(
                    name="email")
            md-list-item(
                v-if="get_('mail') && isShowMail")
                span.dt-person__field {{ get_('mail') }}
                dkbr-icon(
                    name="email")
            md-divider


    v-expansion-panel(
        :popout="true")
        v-expansion-panel-content(
            v-if="get_('interests')")
            dkbr-icon.dt-person__title(
                slot="header"
                name="school"
                :text="isENG? 'Scientific interests':'Научные интересы'")
            v-card
                v-card-text.dt-text(v-html="get_('interests')")
        v-expansion-panel-content(
            v-if="get_('general_info')")
            dkbr-icon.dt-person__title(
                slot="header"
                name="settings_ethernet"
                :text="isENG? 'Additional info':'Дополнительная информация'")
            v-card
                v-card-text.dt-text(v-html="get_('general_info')")
        v-expansion-panel-content(
            v-if="get_('tasks')")
            dkbr-icon.dt-person__title(
                slot="header"
                name="settings_ethernet"
                :text="isENG? 'Current activities in the Laboratory':'Текущая деятельность в лаборатории'")
            v-card
                v-card-text.dt-text(v-html="get_('tasks')")
        v-expansion-panel-content(
            v-if="true")
            dkbr-icon.dt-person__title(
                slot="header"
                name="local_library"
                :text="isENG? 'Publications':'Научные публикации'")
            md-tabs(
                md-theme="th2i"
                :md-active-tab="undefined")
                template(
                    slot="md-tab"
                    slot-scope="{ tab }")
                        | {{ tab.label }}
                        i.badge(v-if="tab.data.count") {{ tab.data.count }}
                md-tab(
                    v-if="papersCount(y)"
                    v-for="y of [2013, 2014, 2015, 2016, 2017]"
                    :id="`papers_${y}`"
                    :md-label="y"
                    :md-template-data="{ count: papersCount(y) }")
                    dt-papers(
                        :man="person"
                        :years="[y]")
</template>

<style lang="sass">

@import '~@dt/sass/root.sass'

.dt-person
    width: 100%
    min-height: 100vh
    &__main
        +flex(row, wrap-reverse, space-between, flex-end)
        &-photo
            flex: 2 1 250px
            +width-max(400px)
            height: 400px
        &-info
            flex: 1 0 250px
            +margin(auto)
            +padding(40px)
    &__title
        +color-font(dark3)
        +font(base-bold-big)
        //+font(base-big)
    &__field
        +font(base)
    &__supl
        width: 100%
        margin: 0
        &-title
            +color-font(dark3)
            +font(base-bold-big)
            +font(base-big)
        &-item
            width: 500px
            padding: 20px
            overflow: scroll
            white-space: wrap
    &__icon
        margin-right: 20px
        +color-font(dark3)
.badge
    width: 23px
    height: 23px
    display: flex
    justify-content: center
    align-items: center
    position: absolute
    top: 2px
    right: 2px
    background: red
    border-radius: 100%
    +color(dark3, light3)
    font-size: 10px
    font-style: normal
    font-weight: 600
    letter-spacing: -.05em
    font-family: 'Roboto Mono', monospace
</style>



