<script>

import Team from '@dt/components/db/team/Team'

export default {
    name: 'dt-page-team',
    path: 'team',
    title: 'Team',
    head: {
        title() {
            return {
                inner: this.isENG ? 'Team' : 'Команда',
                separator: '|',
                complement: 'DT',
            }
        },
        meta() {
            return [
                {
                    name: 'keywords',
                    content: this.isENG
                        ? 'Tensor networks, deep learning, neural networks, megagrant, skoltech, Andrzej Cichocki, Ivan Oseledets,  Victor Lempitsky,  Dmitry Vetrov'
                        : 'Тензорные сети, глубинное обучение, глубокое обучение, нейронные сети, мегагрант, Сколтех, Анджей Чихоцкий, Иван Оселедец, Виктор Лемпицкий, Дмитрий Ветров',
                },
                {
                    name: 'description',
                    content: this.isENG
                        ? 'Skoltech | Laboratory of Prof. Andrzej Cichocki | Tensor networks and deep learning for applications in data mining | Team'
                        : 'Сколтех | Лаборатория Тензорные сети и глубинное обучение для интеллектуального анализа данных под руководством доктора Анджея Чихоцкого | Команда',
                },
            ]
        },
        link: [
            {
                rel: 'icon',
                href: 'static/pictures/dt/logo/favicon.png',
                sizes: '16x16',
                type: 'image/png',
            },
        ],
    },
    components: {
        'dt-team': Team,
    },
}
</script>

<template lang="pug">

dt-team.page-team
</template>



