<script>

import DbMixin from './../_DbMixin'
import Event from './Event'

export default {
    name: 'dt-events',
    mixins: [DbMixin],
    components: {
        'dt-event': Event,
    },
    props: {
        isOnlyImportant: {
            type: Boolean,
            default: _ => false,
        },
    },
    data() {
        return {
            CLASS: 'Events',
            isEventActive: false,
            event_: {},
        }
    },
    computed: {
        isFirst() {
            const i = this.eventsTbl.rows.findIndex(ev => ev == this.event_)
            return i == 0
        },
        isLast() {
            const i = this.eventsTbl.rows.findIndex(ev => ev == this.event_)
            return i == (this.eventsTbl.rows.length - 1)
        },
    },
    methods: {
        filter_(ev) {
            if(!ev.active)
                return false
            if(!ev.important && this.isOnlyImportant)
                return false
            return true
        },
        open(event_) {
            this.event_ = event_
            this.isEventActive = !!this.event_
        },
        openNext() {
            if(!this.event_)
                return
            let i = this.eventsTbl.rows.findIndex(ev => ev == this.event_)
            return this.open(this.eventsTbl.rows[i+1])
        },
        openPrev() {
            if(!this.event_)
                return
            let i = this.eventsTbl.rows.findIndex(ev => ev == this.event_)
            return this.open(this.eventsTbl.rows[i-1])
        },
        close() {
            this.event_ = undefined
            this.isEventActive = false
        },
    }
}
</script>

<template lang="pug">

div.dt-events
    dkbr-progress.dt-events__progress(
        v-if="!eventsTbl.isLoaded || !eventsTbl.isParsed"
        :isThInv="eventsTbl.isLoaded ? false : true"
        :isInf="true")
    div(
        v-if="isEventActive && event_")
        div.dt-events__slider.slider
            div.slider__button.slider__button_arrow-left(
                v-if="!isFirst" @click="openPrev()")
                dkbr-icon(name="keyboard_arrow_left" :size="2")
                dkbr-tooltip(dir="right"
                    :text="isENG ? 'To the previous' : 'К предыдущему'")
            div.slider__button.slider__button_close(
                v-if=" isFirst" @click="close()")
                dkbr-icon(name="close" :size="2")
                dkbr-tooltip(dir="bottom"
                    :text="isENG ? 'Close' : 'Закрыть'")
            div.slider__dots
                div.slider__button.slider__button_dot(
                    v-if="filter_(ev)"
                    v-for="ev of eventsTbl.rows"
                    :key="ev.id"
                    :class=`{'slider__button_dot_active': ev == event_}`
                    @click="open(ev)")
                    dkbr-tooltip(dir="bottom"
                        :text="isENG ? ev.title_short : ev.title_short_rus")
            div.slider__button.slider__button_arrow-right(
                v-if="!isLast" @click="openNext()")
                dkbr-icon(name="keyboard_arrow_right" :size="2")
                dkbr-tooltip(dir="right"
                    :text="isENG ? 'To the next' : 'К следующему'")
            div.slider__button.slider__button_close(
                v-if=" isLast" @click="close()")
                dkbr-icon(name="close" :size="2")
                dkbr-tooltip(dir="bottom"
                    :text="isENG ? 'Close' : 'Закрыть'")
        dt-event.dt-events__event.dt-events__event_f(
            :event="event_" :isShowContent="true")
    dt-event.dt-events__event(
        v-if="!isEventActive && filter_(ev)"
        v-for="ev of eventsTbl.rows"
        :key="ev.id"
        :event="ev"
        @click="_ => open(ev)")
</template>

<style lang="sass">

@import '~@dt/sass/root.sass'

.dt-events
    width: 100%
    +flex(row, wrap, space-around)
    &__progress
        position: absolute
        margin: 0
        padding: 0
    &__event
        flex: 1 1 300px
        +margin(10px)

.slider
    +flex(row, nowrap, space-between)
    +padding(20px)
    &__dots
        +flex(row, wrap, space-around, center)
    &__button
        cursor: pointer
        &_arrow-left
        &_arrow-right
        &_close
        &_dot
            width: 10px
            height: 10px
            margin: 10px
            border: 7px solid rgb(107, 126, 81)
            border-radius: 50%
            &:hover
                border: 7px solid rgb(0, 58, 86)
            &_active
                border: 10px solid rgb(0, 58, 86)
                cursor: default
                &:hover
                    border: 10px solid rgb(0, 58, 86)
</style>



