import axios from 'axios'

import Table from '@@/database/Table'

const DEBUG = true
const TEST = true
const tm$ = dt => new Promise(r => setTimeout(_ => r(), dt))
const DB = {
    team: new Table('team'),
    journals: new Table('journals'),
    papers: new Table('papers'),
    plan: new Table('plan'),
    events: new Table('events'),
}

export default {
    data() {
        return {
            DB,
            isReady: false,
            isReadyAll: false,
            isLoading: false,
            error: undefined,
            tblNamesLoaded: [],
        }
    },
    created() {
        return Promise.all((this.TBL_NAMES || [])
            .map(tblName => this.download$(tblName)))
    },
    methods: {
        filter_(row) {
            return true
        },
        sort_(tbl) {
            return
        },
        parse$(tblName) {
            return new Promise(resolve => {
                if(this.isReady)
                    return resolve()
                this.DB[tblName].rows.forEach(row => {
                    if(this.filter_(row))
                        this[tblName].push(row)
                })
                this.sort_(this[tblName])
                this.isReady = true
                this.CS.debug(`Parsed`, this.CLASS, 'parse$')
                return resolve()
            })
                .catch(err => {
                    this.error = err
                    this.CS.err(
                        `Error for "${tblName}"`, this.CLASS, 'parse$', err)
                })
        },
        download$(tblName) {
            const tbl = this.DB[tblName]
            if(tbl.isReady || tbl.isLoading)
                return Promise.resolve()
            this.CS.debug(
                `Start loading "${tblName}"`,
                this.CLASS, 'download$')
            //const isDev = process.env.NODE_ENV == 'development'
            //const base = isDev ? '/' : TEST ? '/deep-test/' : ''
            const dbPath = 'static/db/dt/'
            tbl.isReady = false
            tbl.isLoading = true
            return axios.get(dbPath + tbl.name + '.json')
                .then(res => {
                    tbl.parse(res.data)
                    tbl.isReady = true
                    tbl.isLoading = false
                    this.tblNamesLoaded.push(tblName)
                    this.CS.debug(
                        `Table "${tblName}" is loaded`,
                        this.CLASS, 'download$')
                    return tbl
                })
                .catch(err => {
                    this.error = err
                    this.CS.err(
                        `Error while loading table "${tblName}"`,
                        this.CLASS, 'download$', err)
                })
        },
    }
}



