            return {
                inner: this.isENG ? 'Home' : 'Главная',
                separator: '|',
                complement: 'DT',
            }
        },
        meta() {
            return [
                {
                    name: 'keywords',
                    content: this.isENG
                        ? 'Tensor networks, deep learning, neural networks, megagrant, skoltech, Andrzej Cichocki, Ivan Oseledets,  Victor Lempitsky,  Dmitry Vetrov'
                        : 'Тензорные сети, глубинное обучение, глубокое обучение, нейронные сети, мегагрант, Сколтех, Анджей Чихоцкий, Иван Оселедец, Виктор Лемпицкий, Дмитрий Ветров',
