                },
            ]
        },
        link: [
            {
                rel: 'icon',
                href: 'static/pictures/dt/logo/favicon.png',
                sizes: '16x16',
                type: 'image/png',
            },
        ],
    },
    components: {
        'dt-events': Events,
    }
