                p.dt-ink.dt-title
                    span(v-if="isENG") Doctor Andrzej Cichocki
                    span(v-if="isRUS") Доктор ф.-м. наук<br>Анджей Чихоцкий
                    br
                    span(v-if="isENG") Head of the Laboratory
                    span(v-if="isRUS") Руководитель Лаборатории
        section.dt-page-index__main-text1
            p.dt-page-index__text.dt-text
                span(v-if="isENG") Laboratory Tensor networks and deep learning for applications in data mining under the guidance of Prof. Andrzej Cichocki on the basis of the <a class="dt-link" href="http://www.skoltech.ru/en/" target="_blank">Skolkovo Institute of Science and Technology</a> develops new fundamental approaches for training, testing and storing parameters of deep neural networks based on tensor decomposition techniques. These approaches allow to reduce by orders of magnitude computational complexity and required memory for the operation of the network, while maintaining a high quality of prediction.
                span(v-if="isRUS") Лаборатория Тензорные сети и глубинное обучение для интеллектуального анализа данных под руководством Проф. Анджея Чихоцкого на базе  <a class="dt-link" href="http://www.skoltech.ru/?lang=ru" target="_blank">Сколковского института науки и технологий</a> занимается разработкой новых подходов, основанных на тензорных разложениях, для построения, обучения, использования и хранения параметров глубинных нейронных сетей, позволяющих на порядки снизить вычислительную сложность и объемы памяти, требуемые для работы с сетью, при высоком качестве предсказания.
        section.dt-page-index__main-text2
            p.dt-page-index__text.dt-text
                span(v-if="isENG") The Laboratory mission is to pursue cutting-edge research in the design and analysis of deep neural networks, tensor decompositions, tensor networks and multiway analysis with many potential practical applications.
                span(v-if="isRUS") Основная задача Лаборатории - проведение передовых исследований в области разработки и анализа глубинных нейронных сетей, тензорных разложений, тензорных сетей и многофакторного анализа с большим количеством потенциальных практических применений.
            p.dt-page-index__text.dt-text
