            +flex-order(20)
            align-self: flex-start
        &-text2
            +flex-order(30)
    &__text
        +padding(10px)
    &__aside
        flex: 0 0 300px
        //+width(300px)
        +flex-basis(300px, 250px, 250px, 200px, auto)
        +margin(0 0 0 30px, $micro: 0 0 0 15px, $nano: 20px 10px 15px auto)
</style>



